<template>
  <div>
    <h3></h3>
    <layout></layout>
    <h3></h3>
    <ul>
      <li>
        <p></p>
        <da-time v-model="dataTime" type="daterange" placeholder=""></da-time>
        <code>dataTime:{{dataTime}}
          <p>type:year/month/date/week/datetime datetimerange/daterange</p>
        </code>
        <da-time-select v-model="time" :time-type.sync="timeType0" :show-time="[3,5,8]" @change="changeTime" @radioChange="(date,type)=>{ }" @inputChange="(date,type)=>{}"></da-time-select>
        <small>show-time210 {{timeType0}}</small>
        <code>time:{{time}}--timeType0{{timeType0}}---
          <el-button @click="timeType0=5"></el-button>
          <el-button @click="time='2019-05-03'"></el-button>
        </code>
        <p>
          <da-time-range v-model="time2" :time-type.sync="timeType" :show-time="[3,5,8]" :label="{sLabel:'',eLabel:''}" :isPickerEnd="isPickerEnd" @change="changeTime2"></da-time-range>
          <code>time2:{{time2}}---timeType:{{timeType}}
            <el-button @click="time2=['2018-01-01','2018-05-03']"></el-button>
            <el-button @click="isPickerEnd=!isPickerEnd">--isPickerEnd:{{isPickerEnd}}</el-button>
            <p>:label="{sLabel:'',eLable:''}"</p>
          </code>
        </p>
        <p>
          <da-cus-time v-model="timeSpan" ref="cusTime" :time-type.sync="timeCycle" @change="cusTimeHandle"></da-cus-time>
          <code>timeSpan:{{timeSpan}}---timeCycle:{{timeCycle}}
            <p>raido[timeRadio]----
            </p>
          </code>
        </p>
      </li>
      <li>
        <p>radio/select</p>
        <p>
          <da-radio-select v-model="contaminant" :maxNum="5" :option-lists="contaminants" label="name" value-key="id" @change="changeCont"></da-radio-select>
          <code>contaminant:{{contaminant}}</code>
        </p>
        <small>option-listsmaxNum,radioselect,</small>
        <p>
          <da-radio-select v-model="contaminant2" :maxNum="3" :option-lists="contaminants" label="name" value-key="id" @change="changeCont"></da-radio-select>
          <code>contaminant2:{{contaminant2}}
            <p>contaminants.lengthmaxNumradio</p>
          </code>
        </p>
      </li>
      <li>
        <p>
          <da-radio-block v-model="radioBlock" :data-lists="contaminants" label="name" value-key="id" @change="radioChange"></da-radio-block>
          <code>radioBlock:{{radioBlock}}</code>
        </p>
      </li>
      <li>
        <p>{{treeValue}}</p>
        <da-tree v-model="treeValue" :load="true" :data-lists="treeData" :default-expand="true" :show-checkbox="true" @currentChange="treeChange"></da-tree>
        <code>treeValue:{{treeValue}}
          <p>currentChange</p>
          <p>showCheckboxBoolean,)expandOnClickNodeBoolean,checkStrictlyBoolean,defaultKeys</p>
        </code>
      </li>
    </ul>
    <h3></h3>
    <ul>
      <li>
        <p>dateTime</p>
        <da-line-chart id="noTimelineChart" type="line" :data-lists="lineChartData" x-axis-key="date" value-key="value" x-axis-format="YYYY-MM"></da-line-chart>
        <p>/</p>
        <da-line-chart id="TimelineChart" type="line" :date-time="time" :data-lists="lineChartData" format="YYYY" x-axis-key="date" value-key="value" x-axis-format="YYYY-MM"></da-line-chart>
        <p></p>
        <da-line-chart id="lineChart" :date-time="time" :data-lists="lineChartData" title="" label="date" x-axis-key="date" value-key="value" format="YYYY" x-axis-format="MM" legend-format="YYYY"></da-line-chart>
        <code>
          time:{{time}}
          <ul>
            <li>
              <p>type="line/bar"</p>
            </li>
            <li>
              <p>dateTime [Array,String]:x"2018-08"131"2018-08-01,2018-08-13"113</p>
            </li>
            <li>
              <p>/x,ynyAxis(NumberArray:20180919y,[2,0,1]seriesyAxisIndex:2,0)hasDataZoom </p>
            </li>
            <li>
              <p>format(dateTimex[])xxAxisFormat,legendlegendFormat
              </p>
            </li>
            <li>
              <p>20180831(lineData)targetData/bechemark{taget:[],banchmark:[],quota:[]}
              </p>
              <small>&nbsp;&nbsp;&nbsp;lineDatadataLists</small>
              <p>20180904lineDataAttr:[ {name: "", yIndex: 0, attr: "quota", color: "#c23531"}...]
              </p>
              <p>20180919,this.$refs.XXXChart.changeTarget({taget:[],banchmark:[],quota:[]});
              </p>
              <p>
                <strong>
                  <a class="link-a" @click="toRoute('comprehensiveTargetAnalysis')"></a>
                </strong>
              </p>
            </li>
          </ul>
        </code>
      </li>
      <li>
        <p></p>
        <da-gauge-chart id="gaugeChart" title="" width="400px" height="210px" :date-time="time" :data-lists="reduceChartData"></da-gauge-chart>
      </li>
      <li>
        <p></p>
        <da-pie-chart id="pieChart" title="" :data-lists="pieData" value-key="value"></da-pie-chart>
      </li>
    </ul>
    <h3></h3>
    <ul>
      <li>
        <p>mode:horizontal / vertical</p>
        <alarm-data :dataLists="contaminants" label="name" value-key="id" mode="horizontal"></alarm-data>
        <alarm-data :dataLists="contaminants" label="name" value-key="id" mode="vertical"></alarm-data>
      </li>
      <li>
        <p></p>
        <div>
          <carbon-data :data-lists="carbonData" width="25%" bgColor="#fff">
            <p slot="add" slot-scope="scope">{{scope}}</p>
            <!-- <div slot-scope="scope">{{scope.data}}</div> -->
          </carbon-data>
        </div>
      </li>
    </ul>
    <h4></h4>
    <ul>
      <li>
        <p>,table-list</p>
        <da-table class="analysis-table" :colums="columnData" :table-list="dataLists" @callBack="getData"></da-table>
      </li>
      <li>
        <p>table-list</p>
        <da-table class="analysis-table" :colums="columnData" :table-list="dataLists.records"></da-table>
      </li>
    </ul>
    <h4></h4>
    <da-calendar></da-calendar>
  </div>
</template>

<script>
import alarmData from "../template/alarmData";
import daCusTime from "../template/customTimeSelect";
import daCalendar from "./example/calendar"; // 
import layout from "./example/layout"; // 
import carbonData from "./components/carbonData";
export default {
  components: { layout, alarmData, daCusTime, daCalendar, carbonData },
  data() {
    return {
      //
      dataTime: ["2018-01-01", "2018-10-01"],
      time: "",
      timeType0: 8,
      time2: [],
      timeType: 5,
      isPickerEnd: false,
      timeSpan: ["2018-05-04", "201-06-01"],
      timeCycle: 5,
      treeValue: "",
      contaminant: "",
      contaminant2: "",
      contaminants: [],
      treeData: [],
      radioBlock: "",
      carbonData: [],
      //   
      lineChartData: [],
      reduceChartData: {},
      pieData: [],
      // 
      columnData: [
        {
          label: "",
          prop: "name"
        },
        {
          label: "",
          prop: "value"
        }
      ],
      dataLists: {}
    };
  },
  methods: {
    changeTime(date, type) {
      //   
      console.log(date, type);
      this.changeChartData();
    },
    changeTime2(date, type) {
      //   
      console.log(date, type);
    },
    cusTimeHandle() {},
    changeCont(value) {
      console.log(value);
    },
    changeCont2(value) {
      console.log(value);
    },
    treeChange(node) {
      // 
      if (!node.obj.children) {
        console.log(node);
      }
    },
    radioChange(data) {
      console.log(data);
    },
    // 
    getData() {
      let name = ["", "", "", ""];
      let word = ["CO", "SO", "NOX", "Smoke"];
      let data = [];
      this.$each(name, (item, index) => {
        // radio
        data.push({
          name: item,
          id: index
        });
      });
      this.contaminants = data;

      let tree = [
        {
          name: "",
          id: 0,
          children: []
        }
      ];
      this.$each(name, (item, index) => {
        tree[0].children.push({
          name: item,
          id: index,
          children: [
            {
              name: word[index],
              id: 10 + index
            }
          ]
        });
      });
      this.treeData = tree;

      this.dataLists = {
        aspnetPager: {
          currentPage: 1,
          startIndex: 0,
          endIndex: 10,
          pageSize: 10,
          totalCount: 4,
          totalPage: 0
        },
        records: [
          { name: "20178", value: "156635.36 kWh" },
          { name: "20168", value: "156635.36 kWh" },
          { name: "", value: "5516.24" }
        ]
      };
      setTimeout(() => {
        this.changeChartData();
      }, 200);
    },
    getReduceData() {
      let name = ["", "", "", ""];
      let word = ["CO", "SO", "NOX", "Smoke"];
      let data = [];
      this.$each(name, (item, index) => {
        data.push({
          name: item,
          word: word[index],
          data: (Math.random() * 300).toFixed(2),
          ratio: (5 - Math.random() * 10).toFixed(2),
          unit: ""
        });
      });
      this.carbonData = data;
    },
    changeChartData() {
      let names = ["", "", "", ""];
      let word = ["CO", "SO", "NOX", "Smoke"];
      let curName = names[this.contaminant];
      let line = [];
      this.$each(new Array(parseInt(Math.random() * 12)), (item, index) => {
        if (10 - Math.random() * 100 < 0) {
          line.push({
            date: new Date().getTime() + (index - 7) * 1000 * 60 * 60 * 24 * 30, //"2018" + "-0" + (index + 1) + "-01",
            name: curName,
            value: parseInt(Math.random() * 100)
          });
          line.push({
            date:
              new Date().getTime() +
              (index - 7) * 1000 * 60 * 60 * 24 * 30 -
              1000 * 60 * 60 * 24 * 30 * 12, //"2017" + "-0" + (index + 1) + "-01",
            name: curName,
            value: parseInt(Math.random() * 100)
          });
        }
      });
      this.lineChartData = line;
      this.reduceChartData = {
        min: 0,
        max: parseInt(1000 + Math.random() * 1000),
        value: Math.random() * 1000,
        unit: "(t)"
      };
      let pie = [];
      this.$each(names, item => {
        pie.push({
          name: item,
          value: (Math.random() * 100).toFixed(2)
        });
      });
      this.pieData = pie;
    },
    changeHandle() {},
    // 
    toRoute(path) {
      this.$router.push({ path });
    }
  },
  mounted() {
    this.getData();
    this.getReduceData();
    this.timeType = 8;
  }
};
</script>

<style>
code {
  display: block;
  border: 1px solid #ddd;
  background-color: #f5f5f5;
  padding: 15px;
  margin: 10px 0;
  line-height: 1.5em;
}
code ul {
  list-style: square;
  padding-left: 20px;
}
</style>




